
.stars {
  margin-left: 1px;
  display: flex;
}

.stars__star {
  height: 16px;
  width: 16px;
  background: url(./images/star.svg) no-repeat;
}

.stars__star:not(:last-child) {
  margin-right: 4px;
}

.stars--5 > .stars__star,
.stars--1 > .stars__star:first-child,
.stars--2 > .stars__star:nth-child(-n + 2),
.stars--3 > .stars__star:nth-child(-n + 3),
.stars--4 > .stars__star:not(:last-child) {
  background: url(./images/star-active.svg) no-repeat;
}
